import helper from '@/common/js/helper.js'
import plate_input from '@/components/plate-input.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
let signature = {
	ctx: null,
	drawing: false,
	lastPos: {
		x: 0,
		y: 0
	},
	currentPos: {
		x: 0,
		y: 0
	},
}

export default {
	components: {
		plate_input,
		uniPopup,
	},
	data() {
		return {
			colors: ['#ff0000', '#333333', '#666666', '#999999'],
			color: '#333333',
			placeholder: '车牌号：',
			showPlateInput: false,
			waiguan: [],
			record: {
				plateNumber: '',
				vin_url: '',
				clerk_sign_url: '',
				user_sign_url: '',
				waiguan:[],
			},
			sign_plate: {
				width: '750rpx',
				height: '250rpx'
			}
		}
	},
	watch: {
		"record.plateNumber"(v) {
			if (v === '') {
				this.placeholder = '车牌号：'
			} else {
				this.placeholder = ''
			}
		},
	},
	onLoad() {
		this.getWaiguanItems()
	},
	methods: {
		getWaiguanItems() {
			var _this = this
			helper.ajax.get('/v1/dict/waiguan', null, res => {
				console.log(res)
				if (res.status === 200) {
					_this.waiguan = res.data
					return
				}
				uni.showToast({
					title: res.message,
					icon: 'none'
				})
			})
		},
		onPlateClick() {
			this.showPlateInput = true
		},
		onPlateClose() {
			this.showPlateInput = false
		},
		onPlateChange(v) {
			this.record.plateNumber = v
		},
		onVinChange(t) {
			this.onPlateClose()
			if (t) {
				let _this = this
				uni.chooseImage({
					count: 6, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					success: function(res) {
						_this.record.vin_url = res.tempFilePaths[0];
					}
				});
			}
		},
		onUserSignChange(t) {
			this.onPlateClose()
			if (t) {
				this.$refs['signature'].open()
				this._resetCanvas()
			}
		},
		onTouchstart(e) {
			signature.drawing = true;
			signature.lastPos = this._getPosition(e)
			signature.currentPos = this._getPosition(e)
			this._renderCanvas()
		},
		onTouchmove(e) {
			signature.currentPos = this._getPosition(e)
			this._renderCanvas()
		},
		onTouchend(e) {
			signature.drawing = false
		},
		onOk() {
			this._getDataURL()
			this.$refs['signature'].close()
		},
		_getPosition(e) {
			let pos = e.changedTouches[0]
			return {
				x: pos.x,
				y: pos.y,
			}
		},
		// Render the signature to the canvas
		_renderCanvas() {
			if (signature.drawing) {
				signature.ctx.moveTo(signature.lastPos.x, signature.lastPos.y)
				signature.ctx.lineTo(signature.currentPos.x, signature.currentPos.y)
				signature.ctx.stroke()
				signature.ctx.draw(true)
				signature.lastPos = signature.currentPos;
			}
		},
		// Reset the canvas context
		_resetCanvas() {
			if (!signature.ctx) {
				signature.ctx = uni.createCanvasContext('firstCanvas')
			}
			signature.ctx.clearRect(0, 0, 750, 250)
			signature.ctx.draw(true)
			signature.ctx.setStrokeStyle(this.color)
			signature.ctx.setLineWidth(3)
			signature.ctx.save()
		},
		_getDataURL() {
			let _this = this
			uni.canvasToTempFilePath({
				canvasId: 'firstCanvas',
				success(res) {
					if (res.errMsg === 'canvasToTempFilePath:ok') {
						_this.record.user_sign_url = res.tempFilePath
					} else {
						console.log(res.errMsg)
					}
				}
			})
		},
		onColorChange(color) {
			this.color = color
			signature.ctx.setStrokeStyle(this.color)
		}
	}
}
